import React from 'react';
import {connect} from 'react-redux';

import {adjustPotions} from '../actions';

const AdjustPotionButton = ({paidBy, plusOrMinus, onButtonClick}) => (
	<button 
		onClick={()=>onButtonClick(
			paidBy === 'lmf'?
			0 :
			1,
			plusOrMinus
		)}
	>
		{plusOrMinus === 'plus' ? '+' : '-'}
		1 {paidBy==='lmf' ? 'LMF ' : 'party '} 
		potion
	</button>
);

const mapStateToProps = (state, ownProps) => ({
	paidBy: ownProps.paidBy,
	plusOrMinus: ownProps.plusOrMinus,
})

const mapDispatchToProps = (dispatch, ownProps) => ({
	onButtonClick: (potionsId, plusOrMinus) => {
		dispatch(adjustPotions(potionsId, plusOrMinus))
	}
});
const DisplayAdjustPotionButton = connect(
	mapStateToProps,
	mapDispatchToProps
)(AdjustPotionButton);

export default DisplayAdjustPotionButton;




